import React, { Component, useState } from "react";
import { ProductCard } from "./gadget/style/gadgetStyle";
//import { toJS } from "mobx";
import { DhProperties, getDefaultImage } from "../DhProperties";
import { MobileListCardContainer } from "./gadget/style/gadgetStyle";


export default function MobileListItem({data, onClick, modelLeft ,modelRight}) {
  
  const [imageLoadError,  setImageLoadError] = useState(false);
  const active = (modelLeft && modelLeft.id == data.id) || (modelRight && modelRight.id == data.id) || false;
  
   const onClickSeeAllSpecs = () => {
     try {
      const redirectUrl = typeof (data?.redirectUrl) === "string" ? data?.redirectUrl?.replace("<lang>", lang || "en") : null;
       const title = data?.redirectPageTitle || data?.title || title || "";
       
      if(typeof(newsHuntAction)!=="undefined" && typeof(callEvent)==="function" && typeof(isIos)!=="undefined" && !isIos()  && redirectUrl){
          const url= DhProperties.properties.pwaBaseUrl+"/webItem?webModel="+encodeURIComponent(JSON.stringify({title:title,url:DhProperties.properties.baseUrl+redirectUrl}));
          callEvent(url);
      } else if(redirectUrl){ 
       window.location = DhProperties.properties.baseUrl+redirectUrl;
      }
    }catch(err){
      console.log("MobileListItem: ",err);
    }
  }

    return (
      <MobileListCardContainer className={active?"selectedCard theme-product-card":"theme-product-card"}>
        <tr>
          <td className={data.product=="mobile"?"leftImage":"laptop"}>
            <div className="imgHolder">
              {data.image && <img src={imageLoadError?getDefaultImage(data.product):data.image} alt={data.name} onError={()=>setImageLoadError(true)}/>}
            </div>
            <div onClick={()=>onClick(data.id, data.name, data.image, data.product)}>
              <span className="compareIcon"></span>
              <p className="compare">Compare</p>
            </div>
          </td>
          <td className="details" onClick={onClickSeeAllSpecs}>
            {data.name && <h6>{data.name}</h6> }
            { data.price && <p>â‚¹ {data.price}</p> }
            <ul>
             {data.os && <li>{data.os}</li> }
             {data.camera && <li>{data.camera}</li> }
             {data.processor && <li>{data.processor}</li>}
             {data.display && <li>{data.display}</li> }
             {data.ram && <li>RAM: {data.ram}</li>}
             {data.storage && <li>STORAGE: {data.storage}</li> }
             {data.storage_ssd && <li>SSD: {data.storage_ssd}</li> }
             {data.resolution && <li>{data.resolution}</li> }
             {data.more_features && <li>{data.more_features}</li> }
            </ul>
            <span>See Full Specs</span>
          </td>
        </tr>
      </MobileListCardContainer>
    );
}

